* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    -webkit-appearance: none;
}

html {
    height: 100%;
}

body {
    height: 100%;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 13px;
    line-height: 140%;
    background-color: #ffffff;
    color: #2A2826;
}


a {
    text-decoration: none;
    color: inherit;
}

img {
    max-width: 100%;
}

button,
input {
    background-color: transparent;
    border: none;
}

button {
    cursor: pointer;
}

input:focus {
    outline: none;

    &::placeholder {
        opacity: 0;
    }
}

input::placeholder {
    transition: .4s;
}

section {
    position: relative;
}

.content {
    overflow: hidden;
}

.container {
    margin: 0 auto;
    position: relative;
    max-width: 1340px;
    padding-left: 20px;
    padding-right: 20px;
    z-index: 10;

    &.--inner {
        padding-left: 120px;
    }
}

.link {
    border-bottom: 1px solid #6A65A4;
    transition: .4s;

    &:hover {
        border-color: transparent;
    }
}

.inner {
    overflow: hidden;
}



.h2 {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 700;
    font-size: 38px;
    line-height: 110%;
    margin-bottom: 40px;
}

.h3 {
    font-family: 'Manrope';
    font-weight: 700;
    font-size: 28px;
    line-height: 110%;
}

.h4 {
    font-weight: 600;
    font-size: 16px;
    line-height: 140%;
    margin-bottom: 7px;
}



.title--center {
    text-align: center;
}


.text-bold {
    p {
        font-weight: 700;
        font-size: 14px;
        line-height: 140%;
        margin-bottom: 22px;
    }
}

.text-grey {
    p {
        color: #2A2826;
        opacity: 0.6;
    }
}