.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding-left: 30px;
  padding-right: 30px;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  border-radius: 2px;
  height: 52px;
  transition: .4s;

  img {
    margin-left: 10px;
  }
}

.btn-red {
  background: #FA0000;
  color: #fff;

  &:hover {
    background: #BF0000;
  }
}

.btn-grey {
  background: #EDEDED;

  &:hover {
    background: #D8D8D8;
  }
}


.main__pic {
  position: relative;

  img {
    display: block;
  }
}

.main__btn {
  position: absolute;
  left: 110px;
  bottom: 80px;
}

.main__list {
  position: absolute;
  right: 20px;
  bottom: 0;
  padding: 45px;
  background: #2A2826;
  border-radius: 60px 0px 0px 0px;
  display: inline-flex;
  justify-content: space-between;
  color: #fff;
  max-width: 775px;
  width: 100%;
  transform: translateY(75px);
}

.main__title {
  font-weight: 700;
  font-size: 45px;
  line-height: 61px;
  position: relative;

  span {
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    vertical-align: top;
    padding-top: 10px;
    display: inline-block;
  }

  &:after {
    content: ">";
    position: absolute;
    right: 100%;
    top: 20px;
    margin-right: 4px;
    font-family: 'Mulish';
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
  }
}

.main__text {
  opacity: .6;
  max-width: 202px;
}


.trast {
  padding: 60px 0;
}

.trast__info {
  .text-grey {
    margin-bottom: 30px;
  }
}

.trast__wrap {
  display: flex;
  justify-content: space-between;
}

.trast__info {
  width: 370px;
}

.trast__list {
  display: flex;
  flex-wrap: wrap;
  width: calc(100% - 460px);
  margin-bottom: -40px;
  padding-top: 80px;
  max-width: 760px;
}

.trast__item {
  width: calc(50% - 15px);
  margin-bottom: 40px;
  display: inline-flex;
  align-items: flex-start;
  cursor: default;

  h4 {
    transition: .4s;
  }

  &:hover {
    .trast__ico img {
      opacity: 0;

      &:last-child {
        opacity: 1;
      }
    }

    h4 {
      color: #FA0000;
    }
  }

  .text-grey {
    max-width: 260px;
  }
}

.trast__ico {
  width: 56px;
  position: relative;


  img {
    max-width: 38px;
    transition: .4s;

    &:last-child {
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
    }
  }

}

.trast__item-info {
  width: calc(100% - 56px);
}

.category {
  padding-bottom: 80px;
}

.category__wrap {
  display: flex;
}

.category__btns {
  width: 300px;
}

.category__btn {
  height: calc(100% / 6);
  width: 100%;
  display: flex;
  align-items: center;
  padding-left: 30px;
  padding-right: 30px;
  background-color: #EDEDED;
  border-right: 4px solid transparent;
  text-align: left;
  transition: .4s;

  &.-is-active {
    background-color: rgba(255, 255, 255, 0.68);
    border-color: #FA0000;
    font-weight: 600;
  }
}

.category__tabs {
  width: calc(100% - 300px);
  background: rgba(237, 237, 237, 0.45);
  border-radius: 0px 0px 60px 0px;
}

.category__tab {
  padding: 44px 67px;
  height: 516px;
  display: flex;
  justify-content: space-between;
  display: none;

  &.-is-active {
    display: flex;
  }

  .text-grey {
    margin-bottom: 43px;
    max-width: 430px;
  }
}

.category__title {
  margin-bottom: 33px;
  max-width: 380px;
}

.category__small-title {
  margin-bottom: 25px;
}

.category__right {
  width: 280px;
}

.list {
  min-height: 300px;
  margin-bottom: 25px;
}

.item {
  list-style: none;
  position: relative;
  padding-left: 27px;
  margin-bottom: 12px;

  &:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 20px;
    height: 20px;
    background: url(../img/check.svg) no-repeat center / contain;
  }
}

.contract {
  padding-bottom: 90px;
}

.contract__title {
  .h2 {
    margin-bottom: 20px;
  }
}

.title-description {
  opacity: .6;
  margin-bottom: 50px;
  max-width: 610px;
}

.contract__list {
  background-color: #EDEDED;
  border-radius: 0px 0px 60px 0px;
  display: flex;
}

.contract__item {
  width: 20%;
  padding-top: 17px;
  padding-left: 15px;
  padding-right: 16px;
  height: 275px;
  text-align: center;
  border-radius: 0px 0px 60px 0px;
  transition: .4s;
  overflow: hidden;

  &:hover {
    background-color: #fff;

    .contract__pic {
      transform: translateY(-20px);

      img {
        opacity: 0;

        &:last-child {
          opacity: 1;
        }
      }
    }

    .contract__info {
      transform: translateY(-50px);
    }

    .contract__title {
      font-weight: 700;
    }

    .contract__text {
      opacity: 1;
    }
  }
}

.contract__pic {
  margin-bottom: 20px;
  position: relative;
  transition: .4s;

  img {
    transition: .4s;
  }

  img:last-child {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 0;
    opacity: 0;
  }
}

.contract__info {
  transition: .4s;
}

.contract__title {
  font-weight: 500;
  font-size: 14px;
  line-height: 140%;
  margin-bottom: 12px;
}


.contract__text {
  font-family: 'Mulish';
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
  transition: .4s;
  opacity: 0;

  span {
    font-weight: 500;
  }
}

.steps {
  padding: 100px 0 100px;
  background: #EDEDED;
  border-radius: 0px 60px 0px 0px;
}

.steps__list {
  display: flex;
  justify-content: space-between;
}

.steps__item {
  width: calc(25% - 25px);

  &:nth-child(1) .steps__panel {
    &:after {
      width: 25%;
    }
  }

  &:nth-child(2) .steps__panel {
    &:after {
      width: 50%;
    }
  }

  &:nth-child(3) .steps__panel {
    &:after {
      width: 75%;
    }
  }

  &:nth-child(4) .steps__panel {
    &:after {
      width: 100%;
    }
  }
}

.steps__number {
  font-family: 'Manrope';
  font-weight: 700;
  font-size: 28px;
  line-height: 110%;
  color: #FA0000;
  padding-bottom: 7px;
}

.steps__panel {
  height: 100%;
  border-top: 3px solid #D5D5D5;
  position: relative;
  padding: 30px 25px;
  background-color: #fff;
  border-radius: 0px 0px 50px 0px;
  transition: .4s;

  &:hover {
    box-shadow: 0px 33px 30px rgba(0, 0, 0, 0.1);
    border-width: 5px;

    &:after {
      height: 5px;
      top: -5px;
    }

    .steps__title {
      color: #FA0000;
    }
  }

  &:after {
    content: "";
    position: absolute;
    top: -3px;
    height: 3px;
    background-color: #FA0000;
    left: 0;
    transition: .4s;
  }



  .text-grey p {
    font-size: 12px;
  }
}

.steps__ico {
  margin-bottom: 20px;
}

.steps__title {
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  margin-bottom: 10px;
}

.gradient-block {
  background: linear-gradient(180deg, #EDEDED 19.01%, rgba(242, 242, 242, 0) 100%);
}

.partners {
  overflow: hidden;

  .h2 {
    margin-bottom: 20px;
  }

  .container {
    padding-top: 100px;
    padding-bottom: 70px;
    position: relative;
    background-color: #fff;
    border-radius: 60px 0 0 0;

    &:after {
      content: '';
      position: absolute;
      left: 100%;
      top: 0;
      width: 2000px;
      height: 100%;
      background-color: #fff;
    }
  }
}

.partners__list {
  display: flex;
  justify-content: space-between;
}

.partners__left,
.partners__right {
  width: calc(50% - 28px);

}

.partners__left {
  .text-grey {
    margin-bottom: 63px;
  }
}

.partners__right {
  .text-grey {
    margin-bottom: 36px;
  }
}


.partners__logos {
  display: flex;
  flex-wrap: wrap;
}

.partners__logo {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 1px solid #EDEDED;
  border-bottom: 1px solid #EDEDED;
  height: 110px;

  &:nth-child(2n) {
    border-radius: none;
  }

  &:nth-last-child(1),
  &:nth-last-child(2) {
    border-bottom: none;
  }

  img {
    max-width: 95%;
  }
}

.slider-text {
  padding: 80px 46px;

  p {
    margin-bottom: 46px;

    &:last-child {
      margin-bottom: 0px;
    }
  }
}

.partners__slider-wrap {
  padding-right: 30px;
  padding-bottom: 30px;
  position: relative;

  &:after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: calc(100% - 123px);
    height: calc(100% - 50px);
    background: rgba(237, 237, 237, 0.74);
    border-radius: 0px 0px 60px 0px;
    z-index: 1;
  }
}

.partners__slider {
  position: relative;
  z-index: 10;
  overflow: hidden;
  background: #fff;
  border: 1px solid #EDEDED;
  border-radius: 0px 0px 32px 0px;

  &:after,
  &:before {
    content: "";
    position: absolute;
    width: 65px;
    height: 58px;
    z-index: 10;
  }

  &:after {
    background: url(../img/slide-arrow-1.svg) no-repeat center / contain;
    left: 13px;
    top: -4px;
  }

  &:before {
    background: url(../img/slide-arrow-2.svg) no-repeat center / contain;
    right: 62px;
    bottom: -5px;
  }
}

.form {
  color: #fff;
  margin-bottom: 80px;

  .container {
    padding-top: 80px;
    padding-bottom: 80px;
    border-radius: 60px 0px 0px 0px;
    background: #2A2826 url(../img/form-bg.svg) no-repeat right -175px top 30px / 567px auto;
  }

  .h2 {
    margin-bottom: 20px;
  }

  .title-description {
    margin-bottom: 30px;
  }
}

.inputs__list {
  display: flex;
  margin-bottom: 30px;
}

.input {
  display: block;
  max-width: 310px;
  width: 100%;
  margin-right: 20px;


  p {
    font-weight: 500;
    font-size: 12px;
    line-height: 140%;
    opacity: .5;
    margin-bottom: 6px;
  }

  input {
    width: 100%;
    height: 48px;
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(255, 255, 255, 0.1);
    padding-left: 15px;
    color: #fff;
  }
}

.form__bottom {
  display: flex;

  .btn {
    margin-right: 28px;
  }
}

.checkbox {
  display: flex;
  align-items: center;

  input {
    display: none;

    &:checked+span:after {
      opacity: 1;
    }
  }

  span {
    width: 22px;
    height: 22px;
    border: 1px solid rgba(231, 234, 239, 0.26);
    background-color: rgba(#fff, .03);
    position: relative;
    margin-right: 12px;

    &:after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 13px;
      height: 9px;
      background: url(../img/check-form.svg) no-repeat center / contain;
      transition: .4s;
      opacity: 0;
    }
  }

  p {
    width: calc(100% - 34px);

    a {
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }
  }
}

.contacts__list {
  display: flex;
}

.contact__info {
  width: 440px;
  padding-right: 20px;

  .text-grey {
    margin-bottom: 50px;
  }
}

.contact {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.contact__ico {
  width: 38px;
}

.contact__text {
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
}

.contact__text--adress {
  font-weight: 500;
  font-size: 14px;
  line-height: 140%;
}

.contact__map {
  height: 510px;
  width: calc(100% - 440px);
  border-radius: 60px 0px 0px 0px;
  overflow: hidden;
}

[class*="ymaps-2-1"][class*="-ground-pane"] {
  filter: grayscale(1);
  -ms-filter: grayscale(1);
  -webkit-filter: grayscale(1);
  -moz-filter: grayscale(1);
  -o-filter: grayscale(1);
}

[class*="ymaps-2-1"][class*="-map-copyrights-promo"] {
  display: none;
}

[class*="ymaps-2-1"][class*="-copyright_logo_no"] {
  display: none;
}