.header {
  .container {
    display: flex;
    justify-content: space-between;
    height: 100px;
    background-color: #fff;
    align-items: center;
  }
}


.logo {
  max-width: 203px;
}

.header__right {
  display: flex;
  justify-content: flex-end;
}


.nav {
  display: flex;
  align-items: center;
  margin-right: 50px;
}

.nav__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  margin-right: 30px;

  &:last-child {
    margin-right: 0px;
  }
}

.callback {
  display: inline-flex;
  padding-left: 30px;
  padding-right: 30px;
  background-color: #EDEDED;
  align-items: center;
  height: 52px;
  font-weight: 600;
  font-size: 16px;
  border-radius: 2px;
  transition: .4s;

  img {
    margin-right: 10px;
  }

  &:hover {
    background: #D8D8D8;
  }
}